import React, { useState } from 'react'
import PropTypes from 'prop-types'
import clsx from 'clsx'
import { makeStyles } from '@material-ui/styles'
import Rating from '@material-ui/lab/Rating';
import {
  Card,
  CardHeader,
  CardContent,
  CardActions,
  Button,
  Divider,
  Table,
  TableBody,
  TableRow,
  TableCell,
  Typography,
  colors,
  Box, Link
} from '@material-ui/core';
import EditIcon from '@material-ui/icons/Edit'
import { get } from 'immutable';
import { Link as RouterLink } from 'react-router-dom';
import { pathUrls } from '../../../../../../../config';

const useStyles = makeStyles(theme => ({
  root: {},
  content: {
    padding: 0
  },
  actions: {
    flexDirection: 'column',
    alignItems: 'flex-start',
    '& > * + *': {
      marginLeft: 0
    }
  },
  buttonIcon: {
    marginRight: theme.spacing(1)
  },
  allignCenter: {
    textAlign: 'center',
    marginTop: '15px'
  }
}))

const UserInfo = props => {
  const { user, statics, title, history,  className, ...rest } = props

  const handlerClick = ()=>{
    history.push(pathUrls.users.viewPath.replace(':id', user.userId))
  }
  const classes = useStyles()

  const getStatics = ()=>{
    console.log(props)
    if(statics){
      return (
        <div className={classes.allignCenter}>
          <Box
            borderColor="transparent"
            component="fieldset"
            mb={3}
          >
            <Typography component="legend">User Rating</Typography>
            <Rating
              readOnly
              value={statics.rating}
            />
          </Box>
        </div>
      )
    }
  }


  return (
    <Card
      {...rest}
      className={clsx(classes.root, className)}
    >
      <CardHeader title={title} />
      <Divider />
      <CardContent className={classes.content}>
        <Table>
          <TableBody>
            <TableRow>
              <TableCell>Name</TableCell>
              <TableCell>
                {user.firstName} {user.lastname}
              </TableCell>
            </TableRow>
            <TableRow selected>
              <TableCell>Phone</TableCell>
              <TableCell>{user.phoneNumber}</TableCell>
            </TableRow>
          </TableBody>
        </Table>
        {getStatics()}
      </CardContent>
      <CardActions className={classes.actions}>
        <Button
          onClick={handlerClick}
        >
          <EditIcon className={classes.buttonIcon} />
          View
        </Button>
      </CardActions>
    </Card>
  )
}

UserInfo.propTypes = {
  className: PropTypes.string,
  customer: PropTypes.object.isRequired
}

export default UserInfo
