import React, { useEffect, useState } from 'react';
import { Redirect } from 'react-router-dom';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/styles';
import { Divider, colors } from '@material-ui/core';

import { Page } from 'components';
import { Header, Summary } from './components';
import { connect } from 'react-redux';
import { brokerage } from '../utils';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(3)
  },
  tabs: {
    marginTop: theme.spacing(3)
  },
  divider: {
    backgroundColor: colors.grey[300]
  },
  content: {
    marginTop: theme.spacing(3)
  }
}));

const BrokerageDetails = props => {
  const { match, history } = props;
  const classes = useStyles();
  const { id } = match.params;
  const [state, setState] = useState({
    brokerage:null
  })
  useEffect(() => {
    brokerage.getBrokerage({
      id:id,
      headers: props.auth.headers,
      success: (response)=>{setState({
        brokerage: response
      })},
      error: (err)=>{console.log(err)}
    })
  }, []);

  return (
    <Page
      className={classes.root}
      title="Customer Management Details"
    >
      <Header />

      <Divider className={classes.divider} />
      <div className={classes.content}>
        {!!state.brokerage && (<Summary
          history={history}
          state={state}
        />)}
      </div>
    </Page>
  );
};

BrokerageDetails.propTypes = {
  history: PropTypes.object.isRequired,
  match: PropTypes.object.isRequired
};

const mapStateToProps = state => {
  return {
    auth: state.session.authentication
  };
};

export default connect(mapStateToProps)(BrokerageDetails);
