import { axios, apiUrls } from 'config';

export class Brokerage {
  static getBrokerageList(config){
    console.log(config)
    const { state, setState, headers } = config;
    Promise.all([
      axios({
        method: apiUrls.brokerage.count.method,
        url: apiUrls.brokerage.count.url,
        headers: headers
      }),
      axios({
        method: apiUrls.brokerage.get.method,
        url: apiUrls.brokerage.get.url,
        params: {
          offset: state.offset,
          limit: state.limit,
          skip: state.offset
        },
        headers: {
          ...headers
        }
      })
    ])
      .then(([count, list]) => {
        setState({
          count: count.data.count,
          brokerage: list.data,
          pages: Math.ceil(count.data.count / state.limit)
        });
        console.log(count.data.count, list.data);
      })
      .catch((error) => {
        console.log(error.response);
      });
  }

  static getBrokerage(config){
    const { id, headers, success, error } = config;
    const options = {
      method: apiUrls.brokerage.get_by_id.method,
      url: apiUrls.brokerage.get_by_id.getAbsoluteUrl(id),
      headers: headers
    };
    axios(options)
      .then(response => {
        console.log('Success', response.data);
        success(response.data);
      })
      .catch(err => {
        error(err);
        console.log('Error', err.response, err);
      });
  }
}
