import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import clsx from 'clsx'
import { makeStyles } from '@material-ui/styles'
import { Grid } from '@material-ui/core'

import axios from 'utils/axios'
import { UserInfo, VehicleDetails, SendEmails, OtherActions } from './components'

const useStyles = makeStyles(() => ({
  root: {}
}))

const Summary = props => {
  const { className, history,  state, ...rest } = props

  const classes = useStyles()

  const getBuyer = () => {
    let buyerInfo = null
    let buyerStatics = null
    if(state.brokerage.hasOwnProperty('buyer')){
      buyerInfo = state.brokerage.buyer
    }
    if(state.brokerage.hasOwnProperty('ratingStatics')){
      if(state.brokerage.ratingStatics.hasOwnProperty('buyerStatics')){
        buyerStatics = state.brokerage.ratingStatics.buyerStatics
      }
    }

    return (
      <Grid
        item
        lg={4}
        md={6}
        xl={3}
        xs={12}
      >
        <UserInfo
          history={history}
          statics={buyerStatics}
          title={'Buyer Info'}
          user={buyerInfo}
        />
      </Grid>
    )

  }
  const getSeller = () => {
    let sellerInfo = null
    let sellerStatics = null
    if(state.brokerage.hasOwnProperty('seller')){
      sellerInfo = state.brokerage.seller
    }
    if(state.brokerage.hasOwnProperty('ratingStatics')){
      if(state.brokerage.ratingStatics.hasOwnProperty('sellerStatics')){
        sellerStatics = state.brokerage.ratingStatics.sellerStatics
      }
    }

    return (
      <Grid
        item
        lg={4}
        md={6}
        xl={3}
        xs={12}
      >
        <UserInfo
          history={history}
          statics={sellerStatics}
          title={'Seller Info'}
          user={sellerInfo}
        />
      </Grid>
    )

  }

  return (
    <Grid
      {...rest}
      className={clsx(classes.root, className)}
      container
      spacing={3}
    >
      <Grid
        item
        lg={4}
        md={6}
        xl={3}
        xs={12}
      >
        <VehicleDetails vehicle={state.brokerage.buyerVehicle} />
      </Grid>
      {getBuyer()}
      {getSeller()}
      {/*<Grid*/}
      {/*  item*/}
      {/*  lg={4}*/}
      {/*  md={6}*/}
      {/*  xl={3}*/}
      {/*  xs={12}*/}
      {/*>*/}
      {/*  <SendEmails customer={customer} />*/}
      {/*</Grid>*/}
      {/*<Grid*/}
      {/*  item*/}
      {/*  lg={4}*/}
      {/*  md={6}*/}
      {/*  xl={3}*/}
      {/*  xs={12}*/}
      {/*>*/}
      {/*  <OtherActions />*/}
      {/*</Grid>*/}
    </Grid>
  )
}

Summary.propTypes = {
  className: PropTypes.string
}

export default Summary
